import { useUser } from "@clerk/clerk-react"
import { useBriefConfigForm } from "@rp/react/common/brief"
import { BriefStepsLegend, VerticalScrollStep } from "@rp/react/web/common/composite-components"
import { Box, Chip, Typography } from "@rp/react/web/mui/core"
import { AccessTime } from "@rp/react/web/mui/icons"
import { BriefStepsContent } from "../BriefStepsContent"
import { BriefNotificationsFrequencyStep } from "./BriefNotificationsFrequencyStep"
import { BriefNotificationsTimeStep } from "./BriefNotificationsTimeStep"

const BriefNotificationsStep = () => {
  const { user } = useUser()
  const {
    form: { schedule },
    setScheduleFromNotification,
  } = useBriefConfigForm()

  return (
    <VerticalScrollStep>
      <BriefStepsLegend>
        <Typography>How often do you want to be notified by email?</Typography>
      </BriefStepsLegend>

      <BriefStepsContent Icon={AccessTime} iconText="Frequency">
        <Box sx={{ mt: 3 }}>
          <Typography color="text.secondary">Email</Typography>
          <Box
            sx={{
              borderRadius: "4px",
              px: "5px",
              py: "5px",
              border: "1px solid",
              borderColor: "overlay.surface",
              "& .MuiChip-root": {
                height: "28px",
                paddingTop: "2px",
                backgroundColor: "overlay.surface",
                borderRadius: "4px",
              },
            }}
          >
            <Chip
              data-testid="brief-notifications-email-address"
              label={user?.primaryEmailAddress?.emailAddress}
            />
          </Box>
        </Box>

        <BriefNotificationsFrequencyStep
          schedule={schedule}
          setSchedule={setScheduleFromNotification}
        />

        <BriefNotificationsTimeStep schedule={schedule} setSchedule={setScheduleFromNotification} />
      </BriefStepsContent>
    </VerticalScrollStep>
  )
}

export { BriefNotificationsStep }
